@import "D:/HTML.CSS/_ProjectInWork/salon/src/components/page-blocks/header.scss";

@import "D:/HTML.CSS/_ProjectInWork/salon/src/scss/_local-fonts.scss";
@import "D:/HTML.CSS/_ProjectInWork/salon/src/scss/_vars.scss";
@import "D:/HTML.CSS/_ProjectInWork/salon/src/scss/base/_01_normalize.scss";
@import "D:/HTML.CSS/_ProjectInWork/salon/src/scss/base/_mixins.scss";
@import "D:/HTML.CSS/_ProjectInWork/salon/src/scss/global/_fonts.scss";
