body {
    background: $bg_body;
}


.quote__block__wrapper {
    background: $beige;
    padding: 50px 0;

    .quote__block {
        display: block;
        max-width: 1000px;
        margin: 0 auto;

        .flex {
            gap: 20px;
            margin-top: 20px;
        }

        .video {
            flex-basis: 40%;
            width: 100%;
        }

        .text {
            flex-basis: 50%;
            font-size: 38px;
        }
    }
}

.text-with-image-block {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .content {
        flex-basis: 50%;
        display: flex;
        flex-direction: column;
        gap: 50px;
        padding: 55px;
        padding-right: 80px;

        .text {
            display: flex;
            flex-direction: column;
            gap: 30px;
        }

        .text2 {
            display: flex;
            flex-direction: column;
            gap: 15px;
        }
    }

    .image {
        flex-basis: 50%;
        display: block;
        flex: 1 1 50%;

    }
}

.staff-slider {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .staffSlider {
        width: 100%;
    }
}

.products__wrapper {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.founder__block {
    margin-top: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .content {
        flex-basis: 65%;
        padding: 40px;

        h2 {
            margin-bottom: 40px;
        }

        .line {
            background-color: $red;
            height: 1px;
            width: 100px;
            margin: 15px 0;
        }

        .founder__about {
            opacity: .7;
        }

        .text {
            display: flex;
            flex-direction: column;
            gap: 20px;
            margin-top: 30px;
        }
    }

    .image {
        flex-basis: 35%;
    }
}

.equipment__block {
    margin-top: 100px;

    .title-with-slider-button {
        margin-bottom: 30px;
    }

    .equipmentSlider {
        .swiper-slide {
            background-color: $beige;
            background-position: 85% bottom;
            background-size: 35%;
            background-repeat: no-repeat !important;
            padding: 55px;
            display: flex;

            .content {
                flex-basis: 50%;
                display: flex;
                flex-direction: column;
                gap: 20px;

                p {
                    font-size: 16px;
                }
            }
        }
    }
}

.about-salon {
    margin-top: 100px;

    h2 {
        margin-bottom: 30px;
    }

    .grid-block {
        margin-bottom: 30px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-gap: 30px;

        .item {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
    }

    .image__block {
        width: 100%;
    }
}

.results__block {
    margin-top: 100px;

    .title-with-slider-button {
        margin-bottom: 30px;
    }
}

.feedbacks-slider {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .feedbacksSlider {
        .swiper-slide {
            min-height: 400px;
            padding: 20px;
            background-color: $beige;
            display: flex;
            flex-direction: column;
            gap: 20px;

            p {
                flex: 1;
                line-height: 135%;
            }

            .name {
                font-family: 'Athelas-Bold';
                font-weight: bold;
                font-size: 24px;
            }
        }
    }
}

.map__block {

    margin-top: 100px;

    h2 {
        margin-bottom: 30px;
    }

    .map-with-block {
        width: 100%;
        background-image: url(../img/map.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        .info__block__wrapper {
            width: 80%;
            margin: 0 auto;
            padding: 100px 0;
            display: flex;
            align-items: center;
            justify-content: end;

            .info__block {
                padding: 40px 30px;
                background-color: $bg_body;
                z-index: 999;
                width: 386px;
                display: flex;
                flex-direction: column;
                gap: 23px;

                .item {
                    margin-left: 35px;
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    font-size: 16px;
                    color: $black;

                    &::before {
                        position: absolute;
                        left: -35px;
                        top: 0;
                        width: 20px;
                        height: 25px;
                    }

                    &:nth-child(1) {
                        &::before {
                            content: url('../img/map_point.png');
                        }
                    }

                    &:nth-child(2) {
                        &::before {
                            content: url('../img/tel.png');
                        }
                    }

                    &:nth-child(3) {
                        &::before {
                            content: url('../img/clock.png');
                        }
                    }

                    &:nth-child(4) {
                        &::before {
                            content: url('../img/mail.png');
                        }
                    }

                    a {
                        font-size: 16px;
                        color: $black;
                    }

                    .mail {
                        font-weight: 700;
                    }
                }

                .item__buttons {
                    display: flex;
                    gap: 10px;
                }
            }
        }
    }
}

.about_doctor__block {
    margin-top: 100px;
    display: grid;
    grid-template-columns: calc(65% - 2.5px) calc(35% - 2.5px);
    gap: 5px;

    &-timetable {
        padding: 30px;
        background-color: $beige;

        .timetable__block {
            width: 100%;
            margin-bottom: 30px;
            border: 2px solid $bg_body;

            h4 {
                padding: 20px;
            }

            .timetable {
                .item {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 8px 20px;

                    &:nth-child(odd) {
                        background-color: $bg_body;
                    }
                }
            }
        }
    }

    &-info {
        padding: 30px;
        background-color: $beige;

        .doctor {
            display: flex;
            align-items: center;
            gap: 25px;

            .image {
                max-width: 240px;
            }
            .name{
                max-width: 50%;
            }
        }

        .tabs-wrapper {
            margin-top: 50px;

            .tab-item{
                display: flex;
                flex-direction: column;
                gap: 20px;
                .flex{
                    .item{
                        &:nth-child(1){
                            flex-basis: 40%;
                        }
                    }
                }
            }

            .tabs {
                display: flex;
                align-items: center;
                padding-bottom: 8px;
                margin-bottom: 20px;
                gap: 10px;
                position: relative;
        
                &::before {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 1px;
                    background-color: #fff;
                }
        
                span {
                    cursor: pointer;
                    white-space: nowrap;
                }
        
                .tab {
                    position: relative;
                    font-weight: 500;
                    font-size: 16px;
                    padding-right: 40px;
                }
        
                .active {
                    color: $red;
        
                    &::before {
                        content: '';
                        position: absolute;
                        width: 100%;
                        height: 1px;
                        bottom: -8px;
                        left: 0;
                        background-color: $red;
                    }
                }
            }
            
        }

    }
}

.price__block {
    margin-top: 100px;
    padding: 0 55px;
    display: flex;
    gap: 30px;

    .mobile__option {

        display: none;
        select {
            width: 100%;
            background-color: $beige;
            padding: 21px 15px;
            font-size: 14px;
            color: $black;
            font-weight: 500;
            border: none;
            outline: none;
            option{
                width: 100%;
            }
        }
    }

    .sidebar {
        background-color: $beige;
        flex-basis: 30%;
        padding: 30px;

        ul {
            display: flex;
            flex-direction: column;
            gap: 8px;

            li {
                position: relative;
                width: 100%;

                a {
                    font-size: 16px;
                    color: $black;
                    font-weight: 400;
                }
            }

            .active {
                a {
                    color: $red;
                }

                &::before {
                    content: '';
                    position: absolute;
                    right: -30px;
                    top: 0;
                    height: 100%;
                    width: 1px;
                    background-color: $red;
                }
            }
        }
    }

    &-info {
        flex-basis: 70%;
        display: flex;
        flex-direction: column;
        gap: 40px;

        .search {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background-color: $beige;
            padding: 22px 30px;

            input {
                background-color: transparent;
                border: none;
                outline: none;
                width: 100%;
                font-size: 20px;

                &::placeholder {
                    font-size: 20px;
                    color: #8D8D8D;
                }
            }
        }

        .price_list {
            display: flex;
            flex-direction: column;
            gap: 40px;

            ul {
                margin-top: 15px;

                .title {
                    padding: 20px 30px;
                    padding-bottom: 14px;

                    span {
                        font-size: 22px;
                        font-weight: bold;
                        font-family: 'Athelas-Bold';
                    }
                }

                li {
                    background-color: $bg_body;
                    padding: 10px 30px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    gap: 10px;

                    &:nth-child(odd) {
                        background-color: $beige;
                    }

                    span {
                        font-size: 16px;
                        font-weight: 400;
                        color: $black;
                    }

                    .price {
                        white-space: nowrap;
                    }
                }
            }
        }
    }
}

.certificate__grid {
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
}

.feedback__grid {
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;

    .item {
        min-height: 400px;
        background-color: $beige;
        padding: 30px;
        display: flex;
        flex-direction: column;
        gap: 10px;

        p {
            flex: 1;
        }

        .flex {
            .name {
                font-size: 22px;
                font-weight: 700;
                font-family: 'Athelas-Bold';
            }

            .magnifire {
                flex-basis: 40%;
                display: flex;
                justify-content: end;
            }
        }
    }
}

.form__block {
    margin-top: 100px;
    background-color: $beige;

    .content {
        gap: 20px;

        form {
            display: flex;
            flex-direction: column;
            gap: 20px;

            input {
                padding: 20px;
                width: 100%;
                background-color: transparent;
                border: 1px solid $black;
                outline: none;
                font-size: 16px;

                &::placeholder {
                    color: #8D8D8D;
                    font-size: 16px;
                }
            }

            textarea {
                padding: 20px;
                resize: none;
                width: 100%;
                background-color: transparent;
                border: 1px solid $black;
                outline: none;
                font-size: 16px;

                &::placeholder {
                    color: #8D8D8D;
                    font-size: 16px;
                }
            }
        }
    }
}

.banner {
    margin: 100px 0;
}

.priority {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 50px;

    .flex {
        gap: 30px;

        .item {
            h4 {
                margin-bottom: 10px;
                margin-left: 20px;
                position: relative;

                &:before {
                    content: '';
                    position: absolute;
                    left: -20px;
                    top: 10px;
                    height: 7px;
                    width: 7px;
                    border-radius: 10px;
                    background-color: $red;
                }
            }
        }
    }
}

.advantages {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 50px;

    .flex {
        gap: 30px;

        .item {
            display: flex;
            flex-direction: column;
            gap: 10px;

            flex-basis: 33%;
        }
    }
}

.cosmetology__block {
    margin: 100px 0;

    display: flex;
    align-items: center;
    background-color: $beige;

    .content {
        flex-basis: 65%;
        display: flex;
        flex-direction: column;
        gap: 15px;
        padding: 0 100px;
    }

    .image {
        flex-basis: 35%;
    }
}

.service_tabs {
    margin-top: 100px;

    .tabs {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 8px;
        margin-bottom: 50px;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: #8D8D8D;
        }

        span {
            cursor: pointer;
        }

        .tab {
            position: relative;
            font-weight: 500;
            font-size: 16px;
        }

        .active {
            color: $red;

            &::before {
                content: '';
                position: absolute;
                width: 100%;
                height: 1px;
                bottom: -8px;
                left: 0;
                background-color: $red;
            }
        }
    }

    .service__tabs_grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 30px;

        .item {
            padding: 30px;
            background-color: $beige;
            display: flex;
            flex-direction: column;
            gap: 30px;

            .fz20 {
                font-size: 20px;
            }

            .text {
                display: flex;
                flex-direction: column;
                gap: 50px;

                p {
                    font-size: 16px;
                }
            }
        }
    }

    .service__block {
        margin-top: 50px;

        .price_list {
            display: flex;
            flex-direction: column;
            gap: 40px;

            ul {
                margin-top: 15px;

                .title {
                    padding: 20px 30px;
                    padding-bottom: 14px;

                    span {
                        font-size: 22px;
                        font-weight: bold;
                        font-family: 'Athelas-Bold';
                    }
                }

                li {
                    background-color: $bg_body;
                    padding: 10px 30px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    &:nth-child(odd) {
                        background-color: $beige;
                    }

                    span {
                        font-size: 16px;
                        font-weight: 400;
                        color: $black;
                    }
                }
            }
        }
    }
}

@import 'media.scss';