@media (max-width: 1024px) {
    .quote__block {
        padding: 0 20px;

        .text {
            font-size: 30px !important;
        }
    }

    .text-with-image-block {
        .content {
            padding: 42px !important;
        }
    }

    .founder__block {
        flex-direction: column;

        .image {
            width: 100%;
            padding: 42px;
        }
    }

    .equipment__block {
        .equipmentSlider {
            .swiper-slide {
                padding: 42px;

                .content {
                    flex-basis: 60% !important;
                }
            }
        }
    }

    .certificate__grid {
        grid-gap: 20px;
        margin-top: 70px;
    }

    .price__block {
        margin-top: 70px;
        gap: 20px;
        padding: 0 42px;

        .price_list {
            ul {
                li {
                    padding: 10px 20px;
                }
            }
        }
    }

    .priority {
        .flex {
            gap: 20px;
            flex-wrap: wrap;

            .item {
                max-width: 330px;
            }
        }
    }

    .banner {
        margin: 70px 0;
    }

    .cosmetology__block {
        margin: 70px 0;

        .content {
            flex-basis: 60%;
            padding: 0 70px;
        }

        .image {
            flex-basis: 40%;
        }
    }

    .about_doctor__block {
        margin-top: 70px;
        grid-template-columns: calc(65% - .5px) calc(35% - .5px);
        gap: 1px;

        &-timetable {
            padding: 20px;
        }

        &-info {
            padding: 20px;

            .doctor{
                .image{
                    max-width: 200px;
                }
            }

            .tabs-wrapper {
                max-width: calc(100vw - 60px);
                .tabs{
                    overflow: scroll;
                    width: 100%;
                }

                .tab-item {
                    .flex {
                        .item {
                            &:nth-child(1) {
                                flex-basis: 50%;
                            }
                        }
                    }
                }

            }

        }
    }

}

@media (max-width: 900px) {
    .about_doctor__block{
        grid-template-columns: 1fr;
    }
}

@media (max-width: 768px) {
    .quote__block {
        .text {
            font-size: 20px !important;
        }
    }

    .text-with-image-block {
        flex-direction: column;

        .content {
            padding: 34px !important;
        }
    }

    .revers {
        flex-direction: column !important;
    }

    .founder__block {
        .image {
            padding: 34px;
        }
    }

    .equipment__block {
        .equipmentSlider {
            .swiper-slide {
                padding: 34px;
                flex-direction: column;
                background-size: 45% !important;

                .content {
                    min-height: 430px;

                    ul {
                        width: 60%;
                    }
                }
            }
        }
    }

    .about-salon {
        .grid-block {
            grid-template-columns: 1fr !important;
            grid-template-rows: auto !important;
        }
    }

    .feedback__grid {
        margin-top: 70px;
        grid-gap: 20px;
    }

    .certificate__grid {
        grid-gap: 20px;
        margin-top: 50px;
        grid-template-columns: repeat(2, 1fr);
    }

    .price__block {
        margin-top: 50px;
        gap: 20px;
        flex-direction: column;
        padding: 0 34px;

        .mobile__option {
            display: block;
        }

        .sidebar {
            display: none;
        }
    }

    .priority {
        margin-top: 50px;

        .flex {
            gap: 20px;
            justify-content: space-around;

            .item {
                max-width: 300px;
            }
        }
    }

    .advantages {
        margin-top: 50px;

        .flex {
            flex-direction: column;
        }
    }

    .banner {
        margin: 50px 0;
    }

    .cosmetology__block {
        margin: 70px 0;

        .content {
            flex-basis: 55%;
            padding: 20px 50px;
        }

        .image {
            flex-basis: 45%;
        }
    }
    .about_doctor__block{
        &-info{
            .doctor{
                .image{
                    max-width: 230px;
                }
            }
        }
    }
}

@media (max-width: 500px) {
    .quote__block {
        .flex {
            flex-direction: column;

        }

        .text {
            font-size: 16px !important;
        }
    }

    .text-with-image-block {
        .content {
            gap: 15px;
            padding: 30px 10px !important;
        }
    }

    .founder__block {
        .image {
            padding: 10px;
        }
    }

    .equipment__block {
        .equipmentSlider {
            .swiper-slide {
                padding: 10px !important;
                background-size: 80% !important;
                background-position: center bottom !important;

                .content {
                    min-height: 700px;

                    ul {
                        width: 100%;
                    }
                }
            }
        }
    }

    .feedback__grid {
        margin-top: 35px;
        grid-template-columns: 1fr;

        .item {
            min-height: 240px;
        }
    }

    .form__block {
        margin-top: 35px;

        .content {
            padding: 30px 10px !important;
        }
    }

    .map__block {
        margin-top: 35px;
    }

    .certificate__grid {
        grid-gap: 20px;
        margin-top: 35px;
        grid-template-columns: 1fr;
    }

    .price__block {
        margin-top: 35px;
        gap: 20px;
        flex-direction: column;
        padding: 0 10px;

        .price_list {
            ul {
                li {
                    padding: 10px 15px;
                }
            }
        }
    }

    .priority {
        margin-top: 35px;
    }

    .advantages {
        margin-top: 35px;
    }

    .cosmetology__block {
        margin: 20px 0;
        flex-direction: column;

        .content {
            flex-basis: 55%;
            padding: 20px 50px;
        }

        .image {
            flex-basis: 45%;
        }
    }

    .about_doctor__block{
        margin-top: 34px;
        &-info{
            .doctor{
                .image{
                    max-width: 145px;
                }
            }
            .tabs-content{
                .tab-item{
                    .flex{
                        flex-direction: column;
                        gap: 15px;
                    }
                }
            }
        }
    }
}