.footer{
    margin-top: 100px;
    padding-bottom: 50px;
    .line{
        width: 100%;
        height: 1px;
        background-color: #4a4a4a;
    }
    .footer__content{
        display: flex;
        justify-content: space-between;
        margin-top: 15px;
        gap: 40px;
        .logo{
            flex-basis: 25%;
            height: 100%;
        }
        .grid__block{
            flex-basis: 70%;
            margin-top: 25px;
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-gap: 40px;
            .item{
                h4{
                    margin-bottom: 15px;
                }
                ul{
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                    li{
                        a{
                            color: $black;
                        }
                    }
                    .mail{
                        margin-top: 15px;
                    }
                }
            }
        }
    }
    .footer-text{
        margin-top: 50px;
        .text{
            flex-basis: 60%;
        }
        a{
            color: $black;
        }
    }
}

@media (max-width: 1100px){
    .footer{
        .footer__content{
            flex-direction: column;
            align-items: center;
            gap: 60px;
            .logo{
                img{
                    max-width: 225px;
                    height: 90px;
                }
            }
        }
        .footer-text{
            display: grid;
            grid-template-columns: 1fr 2fr;
            grid-template-rows: 70px 20px;
            grid-gap: 20px;
            p{
                grid-column: 1/3;
                grid-row: 1/2;
            }
            a{
                &:nth-child(1){
                    grid-column: 1/2;
                    grid-row: 2/3;
                }
                &:nth-child(2){
                    grid-column: 2/3;
                    grid-row: 2/3;
                }
            }
        }
    }
}

@media (max-width: 660px) {
    .footer{
        display: none;
    }
}