.header {
    padding: 20px 30px;

    .burger {
        display: none;
        flex-direction: column;
        gap: 7px;
        width: 30px;

        span {
            background-color: $black;
            height: 1px;
            border-radius: 2px;
        }
    }

    .logo {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 30px;
    }

    .nav__desctop {
        ul {
            display: flex;
            justify-content: space-between;
            gap: 10px;

            li {
                a {
                    color: $black;
                }
            }

            .tel {
                display: flex;
                flex-direction: column;

                a {
                    font-size: 16px;
                    font-weight: 500;
                }

                span {
                    font-size: 12px;
                }
            }

            .dropdown {
                position: relative;
                padding-right: 20px;

                &:hover {
                    ul {
                        display: flex;
                    }
                }

                &::before {
                    content: url('../img/dropdown.png');
                    position: absolute;
                    right: 0;
                    top: 0;
                }

                ul {
                    position: absolute !important;
                    top: 20px;
                    left: -20px;
                    background-color: $bg_body;
                    flex-direction: column;
                    gap: 10px;
                    padding: 20px;
                    width: 200px;
                    display: none;

                }
            }
        }
    }
    .nav__active{
        display: flex !important;
    }
    .nav__mobile {
        background-color: rgba(255, 255, 255, 0.815);
        display: none;
        justify-content: end;


        >ul {
            padding: 30px;
            background-color: #fff;
            display: flex;
            flex-direction: column;
            gap: 28px;
            li{
                a{
                    color: $black;
                }
            }

            .tel {
                margin-bottom: 20px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                div {
                    display: flex;
                    flex-direction: column;

                    a {
                        font-size: 16px;
                        font-weight: 500;
                    }

                    span {
                        font-size: 12px;
                    }
                }
            }
            .dropdown{
                overflow: hidden;
                >a{
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    background-color: #fff;
                    z-index: 10;
                }
                ul{
                    z-index: 1;
                    position: relative;
                    display: none;
                    flex-direction: column;
                    gap: 8px;
                    padding: 12px 0;
                }
            }
            .open{
                ul{
                    display: flex;
                }
            }

        }
    }
}

@media (max-width: 1024px) {
    .header {
        .logo {
            img {
                max-width: 225px;
                max-height: 90px;
            }

        }
    }
}

@media (max-width: 800px) {
    .header {
        .logo__wrapper {
            display: flex;
            align-items: center;
            .image{
              margin-left: -30px;  
            }
        }

        .burger {
            display: flex;
        }

        .logo {
            flex: 1;
            img{
                max-width: 150px;
                max-height: 50px;
            }
        }

        .nav__desctop {
            display: none;
        }
    }
}